import React from 'react';
import { ProductFactory, Utils } from '../../factory';
import { useHistory } from 'react-router-dom';

function ProductForm(props) {

    let history = useHistory();

    const [formType, setFormType] = React.useState('new');
    const [product, setProduct] = React.useState({
        title: '',
        description: '',
        category_id: '',
        price: '',
        currency: '',
        is_active: "0",
        quantity: 0,
        when_sold_out: "",
    });
    const [fileEle, setFileEle] = React.useState();
    const [file, setFile] = React.useState();
    const [newFilePath, setNewFilePath] = React.useState();

    React.useEffect(() => {
        if (props && props.product) {
            setProduct(props.product);
            setFormType('edit');
        } else {
            setFormType('new');
            // default category
            if (props && props.categories) {
                setProduct({...product, category_id: props.categories[0].id});
            }
        }
        
    }, [props]);

    const save = () => {
        const data = new FormData();
        data.append('title', product.title);
        data.append('description', product.description);
        data.append('category_id', product.category_id);
        data.append('price', product.price);
        data.append('currency', product.currency);
        data.append('is_active', product.is_active);
        data.append('quantity', product.quantity);
        data.append('when_sold_out', product.when_sold_out);
        if (file && file.size > 0) {
            data.append('image', file);
        }
        // api
        if (formType === 'edit') {
            data.append('id', product.id);
            Utils.showSpinner();
            ProductFactory.updateProduct(data)
                .then(res => {
                    if (res.status === 200) {
                        Utils.showToast('success', 1500, res.message, '');
                        history.push('/products');
                    } else {
                        Utils.showToast('warning', 1500, res.message, '');
                    }
                    Utils.hideSpinner();
                })
                .catch(err => {
                    console.log('err: ', err);
                    Utils.showToast('error', 1500, "Error Occurred.", '');
                    Utils.hideSpinner();
                })
        } else {
            Utils.showSpinner();
            ProductFactory.addProduct(data)
                .then(res => {
                    console.log('res: ', res);
                    if (res.status === 200) {
                        Utils.showToast('success', 1500, res.message, '');
                        history.push('/products');
                    } else {
                        Utils.showToast('warning', 1500, res.message, '');
                    }
                    Utils.hideSpinner();
                })
                .catch(err => {
                    console.log('err: ', err);
                    Utils.showToast('error', 1500, "Error Occurred.", '');
                    Utils.hideSpinner();
                })
        }
    }

    const remove = () => {
        if (formType === 'edit') {
            ProductFactory.removeProduct(product.id)
                .then(res => {
                    if (res.status === 200) {
                        Utils.showToast('success', 1500, res.message, '');
                        history.push('/products');
                    } else {
                        Utils.showToast('warning', 1500, res.message, '');
                    }
                    Utils.hideSpinner();
                })
                .catch(err => {
                    console.log('err: ', err);
                    Utils.showToast('error', 1500, "Error Occurred.", '');
                    Utils.hideSpinner();
                })
        }
    }

    const handleFileChangeEvent = (event) => {
        var _file = event.target.files[0];
        setFile(_file);
        // pre load image
        var reader = new FileReader();
        var url = reader.readAsDataURL(_file);
        reader.onloadend = function (e) {
            setNewFilePath(reader.result);
        };
    }

  return (
    <div className="ProductForm">
      <div className="row">
        <div className="col-8">
            <div className="card p-3">
                <div className="form-group">
                    <label>Title</label>
                    <input type="text" className="form-control" value={product.title} onChange={(event) => setProduct({...product, title: event.target.value})} />
                </div>
                <div className="form-group">
                    <label>Description</label>
                    <textarea className="form-textarea w-100" value={product.description} onChange={(event) => setProduct({...product, description: event.target.value})}></textarea>
                </div>
            </div>
            <div className="card p-3 mt-3">
                <div className="form-group">
                    <label>Media</label>
                    <div className="row">
                        <div className="col-4 pr-5">
                            <div className="text-center mb-2">Original Upload</div>
                            <img className="w-100" src={product.image} alt="" />
                        </div>
                        <div className="col-4 text-center">
                            <input type="file" className="d-none" 
                                ref={input => setFileEle(input)} 
                                onChange={(event) => handleFileChangeEvent(event)}
                                />
                            <button className="btn btn-primary my-5" onClick={() => fileEle.click()}>Upload Image</button>
                        </div>
                        <div className="col-4 pl-5">
                            <div className="text-center mb-2">New Upload</div>
                            <img className="w-100" src={newFilePath} alt="" />
                        </div>
                    </div>
                </div>
            </div>
            <div className="card p-3 mt-3">
                <h6>Pricing</h6>
                <div className="row">
                    <div className="col-12">
                        <div className="form-group">
                            <label>Price</label>
                            <input type="text" className="form-control" value={product.price} onChange={(event) => setProduct({...product, price: event.target.value})} />
                        </div>
                    </div>
                    {/* <div className="col-6">
                        <div className="form-group">
                            <label>Currency</label>
                            <input type="text" className="form-control" />
                        </div>
                    </div> */}
                </div>
            </div>
        </div>
        <div className="col-4">
            <div className="card p-3">
                <div className="form-group">
                    <label>Category</label>
                    <select className="form-control" value={product.category_id} onChange={(event) => setProduct({...product, category_id: event.target.value})}>
                        {(props && props.categories) && props.categories.map((item, index) => 
                            <option key={index} value={item.id}>{item.title}</option>
                        )}
                    </select>
                </div>
            </div>
            <div className="card p-3 mt-3">
                <div className="form-group">
                    <label>Publish</label>
                    <select className="form-control" value={product.is_active} onChange={(event) => setProduct({...product, is_active: event.target.value})}>
                        <option value="1">Active</option>
                        <option value="0">Inactive</option>
                    </select>
                </div>
            </div>
            <div className="card p-3 mt-3">
                <div className="form-group">
                    <label>Quantity</label>
                    <input className="form-control" type="number" value={product.quantity} onChange={(event) => setProduct({...product, quantity: event.target.value})} />
                </div>
                <div className="form-group">
                    <label>When sold out</label>
                    <select className="form-control" value={product.when_sold_out} onChange={(event) => setProduct({...product, when_sold_out: event.target.value})}>
                        <option value=""> - </option>
                        <option value="Stop selling">Stop selling</option>
                        <option value="Don't show">Don't show</option>
                    </select>
                </div>
            </div>
        </div>
      </div>
      <div className="separator my-3"></div>
        <div className="row mx-0">
            <button className={formType === 'edit' ? "btn btn-md btn-danger mr-auto" : "d-none"} onClick={() => remove()}>Delete product</button>
            <button className="btn btn-md btn-primary ml-auto" onClick={() => save()}>Save</button>
        </div>
    </div>
  );
}

export default ProductForm;
