import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { FaArrowLeft } from 'react-icons/fa';
import PageHeader from '../components/PageHeader';
import { UserFactory } from '../../factory';
import Utils from '../../factory/Utils';

function CustomerNew() {

    let history = useHistory();

    const [name, setName] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [password, setPassword] = React.useState('');
    const [passwordConfirmation, setPasswordConfirmation] = React.useState('');
    const [fileEle, setFileEle] = React.useState();
    const [file, setFile] = React.useState();
    const [newFilePath, setNewFilePath] = React.useState();

    const register = () => {
        const data = new FormData();
        data.append('name', name);
        data.append('email', email);
        data.append('phone', phone);
        data.append('password', password);
        data.append('password_confirmation', passwordConfirmation);
        if (file && file.size > 0) {
            data.append('avatar', file);
        }

        Utils.showSpinner();
        UserFactory.addCustomer(data)
            .then(res => {
                console.log('res', res);
                if (res.status === 200) {
                    Utils.showToast('success', 1500, res.message, '');
                    history.push('/customers');
                } else {
                    Utils.showToast('warning', 1500, res.message, '');
                }
                Utils.hideSpinner();
            })
            .catch(err => {
                console.log('err: ', err);
                Utils.showToast('error', 1500, "Error Occurred.", '');
                Utils.hideSpinner();
            })
    }

    const handleFileChangeEvent = (event) => {
        var _file = event.target.files[0];
        setFile(_file);
        // pre load image
        var reader = new FileReader();
        var url = reader.readAsDataURL(_file);
        reader.onloadend = function (e) {
            setNewFilePath(reader.result);
        };
    }

  return (
    <div className="CustomerNew container">
        <Link className="btn-link text-dark" to={`/customers`}>
            <FaArrowLeft /><span className="pl-2">Customers</span>
        </Link>
        <PageHeader title="Add customer" />
        {/*  */}
        <div className="separator my-3"></div>
        <div className="row">
            <div className="col-4">
                <h6>Customer overview</h6>
            </div>
            <div className="col-8">
                <div className="card p-3">
                    <div className="form-group">
                        <label>Full Name</label>
                        <input type="text" className="form-control" value={name} onChange={(event) => setName(event.target.value)} min="8" max="255" />
                    </div>
                    <div className="form-group">
                        <label>Email</label>
                        <input type="email" className="form-control" value={email} onChange={(event) => setEmail(event.target.value)} min="8" max="255" />
                    </div>
                    <div className="form-group">
                        <label>Phone number</label>
                        <input type="text" className="form-control" value={phone} onChange={(event) => setPhone(event.target.value)} min="8" max="255" />
                    </div>
                </div>
            </div>
        </div>
        <div className="separator my-3"></div>
        <div className="row">
            <div className="col-4">
                <h6>Customer security</h6>
            </div>
            <div className="col-8">
                <div className="card p-3">
                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" className="form-control" value={password} onChange={(event) => setPassword(event.target.value)} min="8" max="255" />
                    </div>
                    <div className="form-group">
                        <label>Password Confirm</label>
                        <input type="password" className="form-control" value={passwordConfirmation} onChange={(event) => setPasswordConfirmation(event.target.value)} min="8" max="255" />
                    </div>
                </div>
            </div>
        </div>
        <div className="separator my-3"></div>
        <div className="row">
            <div className="col-4">
                <h6>Customer photo</h6>
            </div>
            <div className="col-8">
                <div className="card p-3">
                    <div className="row">
                        <div className="col-6">
                            <input type="file" className="d-none" 
                                ref={input => setFileEle(input)} 
                                onChange={(event) => handleFileChangeEvent(event)} 
                                />
                            <button className="btn btn-primary" onClick={() => fileEle.click()}>Upload Avatar</button>
                        </div>
                        <div className="col-3">
                            <img className="w-100" src={newFilePath} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div className="separator my-3"></div>
        <div className="row mx-0">
            <button className="btn btn-md btn-primary ml-auto" onClick={() => register()}>Save</button>
        </div>
        {/*  */}
    </div>
  );
}

export default CustomerNew;
