import React from 'react';
import { Link } from 'react-router-dom';
import { OrderFactory, Utils } from '../../factory';
import PageHeader from '../components/PageHeader';

function Orders() {

  const [searchBy, setSearchBy] = React.useState('order');
  const [searchKey, setSearchKey] = React.useState('');
  const [orders, setOrders] = React.useState(null);

  React.useEffect(() => {
    getOrders(searchKey);
  }, [searchKey]);

  const getOrders = (searchKey) => {
    Utils.showSpinner();
    OrderFactory.getOrders({searchBy: searchBy, searchKey: searchKey})
      .then(res => {
        if (res.status === 200) {
          setOrders(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  return (
    <div className="Orders">
      <PageHeader title="Orders" />
      {/*  */}
      <div className="card">
        <div className="card-header">
          <div className="row mx-0">
            <div className="d-flex ml-auto">
              <label className="search-label">Search By: </label>
              <select className="form-control search-by ml-2"
                onChange={(event) => setSearchBy(event.target.value)}>
                <option value="order">Order</option>
                <option value="date">Date</option>
                <option value="customer">Customer Name</option>
                <option value="price">Price</option>
                <option value="product">Product</option>
              </select>
              <input type="text" 
                className="form-control search-key ml-3" 
                placeholder="Search orders" 
                onKeyUp={(event) => setSearchKey(event.target.value)}
                />
            </div>
          </div>
        </div>
        <div className="card-body p-0">
          <ul className="tb-ul">
            <li className="tb-row">
              <div className="col-2 d-flex">
                Order
              </div>
              <div className="col-2">
                Customer
              </div>
              <div className="col-2">
                Date
              </div>
              <div className="col-2">
                Total
              </div>
              <div className="col-2">
                Payment
              </div>
              <div className="col-1">
                Items
              </div>
              <div className="col-1">
                
              </div>
            </li>
            {orders && orders.map((item, index) => 
              <li className="tb-row" key={index}>
                <div className="col-2 d-flex">
                  <div className="d-block ml-3 my-auto">
                    <div className="row-name font-weight-bold">{item.name}</div>
                  </div>
                </div>
                <div className="col-2">
                  {item.user.name}
                </div>
                <div className="col-2">
                  {item.created_at}
                </div>
                <div className="col-2">
                  {item.currency + item.total_price}
                </div>
                <div className="col-2">
                  <span className={item.is_paid ? "badge badge-pill badge-info" : "badge badge-pill badge-secondary"}>{item.is_paid ? "Paid" : "Unpaid"}</span>
                </div>
                <div className="col-1">
                  {item.order_items.length}
                </div>
                <div className="col-1 text-right">
                  <Link to={`/orders/${item.id}`} className="">View</Link>
                </div>
              </li>
            )}
          </ul>
        </div>
        <div className="card-footer">
          <button className="btn btn-pagination">
            <svg viewBox="0 0 20 20" className="Polaris-Icon__Svg_375hu" focusable="false" aria-hidden="true"><path d="M17 9H5.414l3.293-3.293a.999.999 0 1 0-1.414-1.414l-5 5a.999.999 0 0 0 0 1.414l5 5a.997.997 0 0 0 1.414 0 .999.999 0 0 0 0-1.414L5.414 11H17a1 1 0 1 0 0-2" fillRule="evenodd"></path></svg>
          </button>
          <button className="btn btn-pagination">
            <svg viewBox="0 0 20 20" className="Polaris-Icon__Svg_375hu" focusable="false" aria-hidden="true"><path d="M17.707 9.293l-5-5a.999.999 0 1 0-1.414 1.414L14.586 9H3a1 1 0 1 0 0 2h11.586l-3.293 3.293a.999.999 0 1 0 1.414 1.414l5-5a.999.999 0 0 0 0-1.414" fillRule="evenodd"></path></svg>
          </button>
        </div>
      </div>
      {/*  */}
    </div>
  );
}

export default Orders;
