import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { FaArrowLeft } from 'react-icons/fa';
import PageHeader from '../components/PageHeader';
import { OrderFactory, Utils } from '../../factory';

function OrderDetail() {

    let { id } = useParams();
    const [order, setOrder] = React.useState(null);

    React.useEffect(() => {
        getOrderDetail(id);
    }, [id]);

    const getOrderDetail = (id) => {
        Utils.showSpinner();
        OrderFactory.getOrderDetail(id)
            .then(res => {
                if (res.status === 200) {
                setOrder(res.data);
                } else {
                Utils.showToast('warning', 1500, res.message, '');
                }
                Utils.hideSpinner();
            })
            .catch(err => {
                console.log('err: ', err);
                Utils.showToast('error', 1500, "Error Occurred.", '');
                Utils.hideSpinner();
            })
    }

  return (
    <div className="OrderDetail container">
        <Link className="btn-link text-dark" to={`/orders`}>
            <FaArrowLeft /><span className="pl-2">Orders</span>
        </Link>
        <PageHeader title={order && order.name} />
        {/*  */}
        <div className="row">
            <div className="col-8">
                <div className="card p-3">
                    <h6>Order Detail</h6>
                    {order && order.order_items && order.order_items.map((item, index) => 
                        <div className="row" key={index}>
                            <div className="col-2">
                                <img src={item.product.image} className="w-100" alt="" />
                            </div>
                            <div className="col-6">
                                <div className="row text-primary">{item.title}</div>
                                <div className="row text-secondary">{item.description}</div>
                            </div>
                            <div className="col-2 text-right">
                                {order.currency + item.price} x {item.quantity}
                            </div>
                            <div className="col-2 text-right">
                                {order.currency + item.total_price}
                            </div>
                        </div>
                    )}
                </div>
                <div className="card p-3 mt-3">
                    <h6>Payment</h6>
                    <div className="row mx-0">
                        <label className="mr-auto">Total</label>
                        <span className="ml-auto">{order && (order.currency + order.total_price)}</span>
                    </div>
                    <div className="separator mb-3"></div>
                    <div className="row mx-0">
                        <label className="mr-auto">Paid by customer</label>
                        <span className="ml-auto">{(order && order.is_paid) ? order.currency + order.total_price : "£0"}</span>
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className="card p-3">
                    <h6>Customer</h6>
                    <div className="form-group">
                        <label className="d-block">{order && order.user && order.user.name}</label>
                        <span>
                            {order && order.user && order.user.orders.length 
                                ? (order.user.orders.length > 1 
                                    ? order.user.orders.length + " orders" 
                                    : order.user.orders.length + " order") 
                                : "0 order"}
                        </span>
                    </div>
                    <div className="separator mb-3"></div>
                    <div className="form-group">
                        <label className="d-block">CONTACT INFOMATION</label>
                        <span className="text-priamer">{order && order.user && order.user.email}</span>
                    </div>
                </div>
            </div>
        </div>
        {/*  */}
    </div>
  );
}

export default OrderDetail;
