import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { FaArrowLeft } from 'react-icons/fa';
import PageHeader from '../components/PageHeader';
import CollectionForm from '../components/CollectionForm';
import { Utils, CollectionFactory } from '../../factory';

function CollectionNew() {

  const { id } = useParams();
  const [collection, setCollection] = React.useState(null);

  React.useEffect(() => {
    getCollectionDetail(id);
  }, [id]);

  const getCollectionDetail = (id) => {
    Utils.showSpinner();
    CollectionFactory.getCollectionDetail(id)
        .then(res => {
            if (res.status === 200) {
                setCollection(res.data);
            } else {
                Utils.showToast('warning', 1500, res.message, '');
            }
            Utils.hideSpinner();
        })
        .catch(err => {
            console.log('err: ', err);
            Utils.showToast('error', 1500, "Error Occurred.", '');
            Utils.hideSpinner();
        })
  }

  return (
    <div className="CollectionNew container">
        <Link className="btn-link text-dark" to={`/collections`}>
            <FaArrowLeft /><span className="pl-2">Collections</span>
        </Link>
        <PageHeader title={collection && collection.title} />
        {/*  */}
        <CollectionForm collection={collection} />
        {/*  */}
    </div>
  );
}

export default CollectionNew;
