import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { FaArrowLeft } from 'react-icons/fa';
import PageHeader from '../components/PageHeader';
import ProductForm from '../components/ProductForm';
import { CategoryFactory, ProductFactory } from '../../factory';
import Utils from '../../factory/Utils';

function ProductEdit() {

  let { id } = useParams();
  const [categories, setCategories] = React.useState(null);
  const [product, setProduct] = React.useState(null);

  React.useEffect(() => {
    getCategories();
    getProductDetail(id);
  }, [id]);

  const getCategories = () => {
    Utils.showSpinner();
    CategoryFactory.getCategories()
      .then(res => {
        if (res.status === 200) {
          setCategories(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  const getProductDetail = (id) => {
    Utils.showSpinner();
    ProductFactory.getProductDetail(id)
      .then(res => {
        if (res.status === 200) {
          setProduct(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  return (
    <div className="ProductEdit container">
        <Link className="btn-link text-dark" to={`/products`}>
            <FaArrowLeft /><span className="pl-2">Products</span>
        </Link>
        <PageHeader title={product && product.title} />
        {/*  */}
        <ProductForm product={product} categories={categories} />
        {/*  */}
    </div>
  );
}

export default ProductEdit;
