import env from '../env';
import { POST, GET, DELETE } from './Utils';

const addProduct = (newProduct) => {
    const path = `${env.BASE_API}/admin/products/create`;
    return POST(path, newProduct);
}

const getProducts = () => {
    const path = `${env.BASE_API}/admin/products`;
    return GET(path);
}

const getProductDetail = (id) => {
    const path = `${env.BASE_API}/admin/products/${id}`;
    return GET(path);
}

const updateProduct = (product) => {
    const path = `${env.BASE_API}/admin/products/update`;
    return POST(path, product);
}

const removeProduct = (id) => {
    const path = `${env.BASE_API}/admin/products/${id}`;
    return DELETE(path, null);
}

const search = (param) => {
    const path = `${env.BASE_API}/admin/products/search`;
    return POST(path, param);
}

const ProductFactory = {
    addProduct,
    getProducts,
    getProductDetail,
    updateProduct,
    removeProduct,
    search
}

export default ProductFactory;