import env from '../env';
import { POST, GET, DELETE } from './Utils';

const addCollection = (newCollection) => {
    const path = `${env.BASE_API}/admin/collections/create`;
    return POST(path, newCollection);
}

const getCollections = () => {
    const path = `${env.BASE_API}/admin/collections`;
    return GET(path);
}

const getCollectionDetail = (id) => {
    const path = `${env.BASE_API}/admin/collections/${id}`;
    return GET(path);
}

const updateCollection = (collection) => {
    const path = `${env.BASE_API}/admin/collections/update`;
    return POST(path, collection);
}

const removeCollection = (id) => {
    const path = `${env.BASE_API}/admin/collections/${id}`;
    return DELETE(path, null);
}

const addProductToCollection = (params) => {
    const path = `${env.BASE_API}/admin/collections/addProductToCollection`;
    return POST(path, params);
}

const CollectionFactory = {
    addCollection,
    getCollections,
    getCollectionDetail,
    updateCollection,
    removeCollection,
    addProductToCollection
}

export default CollectionFactory;