import React from 'react';
import { FaTimes } from 'react-icons/fa';
import { ReactSortable } from "react-sortablejs";
import CategoryFactory from '../../factory/CategoryFactory';
import Utils from '../../factory/Utils';
import PageHeader from '../components/PageHeader';

function Categories() {

  const [categories, setCategories] = React.useState([]);
  const [title, setTitle] = React.useState('');
  const [description, setDescription] = React.useState('');

  React.useEffect(() => {
    getCategories();
  }, []);

  const getCategories = () => {
    Utils.showSpinner();
    CategoryFactory.getCategories()
      .then(res => {
        if (res.status === 200) {
          setCategories(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  const handleChangeCategoriesList = (newList) => {
    console.log('new>> ', newList);
    if(!newList.length) {
      setCategories(categories);
      return;
    }
    let ids = [];
    for(let i = 0; i < newList.length; i++) {
      ids.push(newList[i].id);
    }
    let data = {
      "ids": ids.toString()
    }
    CategoryFactory.updateCategoriesPriority(data)
      .then(res => {
        if (res.status === 200) {
          setCategories(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  const addCategory = () => {
    let _newCategory = {
      "title": title,
      "description": description
    }
    Utils.showSpinner();
    CategoryFactory.addCategory(_newCategory)
      .then(res => {
        if (res.status === 200) {
          setCategories(res.data);
          Utils.showToast('success', 1500, res.message, '');
          // refresh inputs
          setTitle('');
          setDescription('');
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  const remove = (id) => {
    Utils.showSpinner();
    CategoryFactory.removeCategory(id)
      .then(res => {
        if (res.status === 200) {
          setCategories(res.data);
          Utils.showToast('success', 1500, res.message, '');
          // refresh inputs
          setTitle('');
          setDescription('');
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  // const edit = (item) => {

  // }

  return (
    <div className="Categories">
      <PageHeader title="Categories" />
      {/*  */}
        <div className="container">
          <div className="row">
            <div className="col-8">
              <div className="card p-3">
                <h5>Add Category</h5>
                <div className="separator mb-3"></div>
                <div className="form-group">
                  <label>Title</label>
                  <input type="text" className="form-control" placeholder="Title" value={title} onChange={(event) => setTitle(event.target.value)} />
                </div>
                <div className="form-group">
                  <label>Description</label>
                    <textarea className="form-textarea w-100" placeholder="description" value={description} onChange={(event) => setDescription(event.target.value)}></textarea>
                </div>
                <div className="separator mb-3"></div>
                <div className="row mx-0">
                  <button className="btn btn-primary ml-auto px-4" onClick={() => addCategory()}>Add</button>
                </div>
              </div>
            </div>
            <div className="col-4">
              <div className="card p-3">
                <h5>Categories</h5>
                <div className="separator mb-2"></div>
                <ReactSortable 
                  animation={200}
                  delayOnTouchStart={true}
                  delay={2}
                  list={categories} 
                  setList={(newList) => handleChangeCategoriesList(newList)}>
                  {categories.map((item) => (
                    <li key={item.id} className="category-item">
                      {item.title}
                      <span className="ml-auto" onClick={() => remove(item.id)}><FaTimes /></span>
                    </li>
                  ))}
                </ReactSortable>
              </div>
            </div>
          </div>
        </div>
      {/*  */}
    </div>
  );
}

export default Categories;
