import env from '../env';
import { POST, GET, DELETE } from './Utils';

const addCategory = (newCategory) => {
    const path = `${env.BASE_API}/admin/categories/create`;
    return POST(path, newCategory);
}

const getCategories = () => {
    const path = `${env.BASE_API}/admin/categories`;
    return GET(path);
}

const removeCategory = (id) => {
    const path = `${env.BASE_API}/admin/categories/${id}`;
    return DELETE(path, null);
}

const updateCategoriesPriority = (ids) => {
    const path = `${env.BASE_API}/admin/categories/updateCategoriesPriority`;
    return POST(path, ids);
}

const CategoryFactory = {
    addCategory,
    getCategories,
    removeCategory,
    updateCategoriesPriority,
}

export default CategoryFactory;