import React from 'react';
import { Link } from 'react-router-dom';
import { UserFactory } from '../../factory';
import Utils from '../../factory/Utils';
import PageHeader from '../components/PageHeader';

function Customers() {

  const [customers, setCustomers] = React.useState(null);

  React.useEffect(() => {
    Utils.showSpinner();
    getCustomers();
  }, []);

  const getCustomers = () => {
    UserFactory.getCustomers()
      .then(res => {
        if (res.status === 200) {
          setCustomers(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      });
  }

  const calcTotalSpent = (orders) => {
    var totalSpent = 0;
    for (var i = 0; i < orders.length; i++) {
      totalSpent += orders[i].total_price;
    }
    return '£' + totalSpent + ' spent';
  }

  return (
    <div className="Customers">
      <PageHeader title="Customers" />
      {/*  */}
      <div className="card">
        <div className="card-header d-flex">
          <h6>All</h6>
          <Link className="btn btn-md btn-primary ml-auto" to={`/customers/new`}>+ Add customer</Link>
        </div>
        <div className="card-body p-0">
          <ul className="tb-ul">
            {/*  */}
            {customers && customers.map((item, index) => 
              <li className="tb-row" key={index}>
                <div className="col-6 d-flex">
                  <div className="row-avatar">
                    <img src={item.avatar} className="avatar" alt="" />
                  </div>
                  <div className="d-block ml-3 my-auto">
                    <div className="row-name font-weight-bold">{item.name}</div>
                    <div className="row-email text-dark">{item.email}</div>
                  </div>
                </div>
                <div className="col-2">
                  {item.orders.length > 1 ? item.orders.length + ' orders' : item.orders.length + ' order'}
                </div>
                <div className="col-2">
                  {calcTotalSpent(item.orders)}
                </div>
                <div className="col-2 text-right">
                  <Link to={`/customers/${item.id}`} className="">View</Link>
                </div>
              </li>
            )}
            {/*  */}
          </ul>
        </div>
        <div className="card-footer">
          <button className="btn btn-pagination">
            <svg viewBox="0 0 20 20" className="Polaris-Icon__Svg_375hu" focusable="false" aria-hidden="true"><path d="M17 9H5.414l3.293-3.293a.999.999 0 1 0-1.414-1.414l-5 5a.999.999 0 0 0 0 1.414l5 5a.997.997 0 0 0 1.414 0 .999.999 0 0 0 0-1.414L5.414 11H17a1 1 0 1 0 0-2" fillRule="evenodd"></path></svg>
          </button>
          <button className="btn btn-pagination">
            <svg viewBox="0 0 20 20" className="Polaris-Icon__Svg_375hu" focusable="false" aria-hidden="true"><path d="M17.707 9.293l-5-5a.999.999 0 1 0-1.414 1.414L14.586 9H3a1 1 0 1 0 0 2h11.586l-3.293 3.293a.999.999 0 1 0 1.414 1.414l5-5a.999.999 0 0 0 0-1.414" fillRule="evenodd"></path></svg>
          </button>
        </div>
      </div>
      {/*  */}
    </div>
  );
}

export default Customers;
