import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

// 
import Header from './components/Layouts/Header';
import Leftbar from './components/Layouts/Leftbar';
import Login from './Auth/Login';
import Dashboard from './Dashboard/Dashboard';
import Orders from './Orders/Orders';
import OrderDetail from './Orders/OrderDetail';
import ProductNew from './Products/ProductNew';
import ProductEdit from './Products/ProductEdit';
import Products from './Products/Products';
import Categories from './Products/Categories';
import Collections from './Products/Collections';
import CollectionNew from './Products/CollectionNew';
import CollectionEdit from './Products/CollectionEdit';
import Tables from './Tables/Tables';
import Customers from './Customers/Customers';
import CustomerDetail from './Customers/CustomerDetail';
import CustomerNew from './Customers/CustomerNew';
import { AuthFactory } from '../factory';
import { EventEmitter } from '../events';

function Admin() {

  const [isLoggedin, setIsLoggedin] = React.useState(true);

  EventEmitter.subscribe('refreshLogin', (event) => {
    setIsLoggedin(event);
  });

  React.useEffect(() => {
    let loginStatus = AuthFactory.getAdminLoginStatus();
    setIsLoggedin(loginStatus);
  }, []);

    return (
      <Router>
        <div className="App-Admin">
          <Switch>
            <Route path="/login">
              <AdminAuth />
            </Route>
            <Route path="/">
              {isLoggedin ? <AdminPanel /> : <AdminAuth />}
            </Route>
          </Switch>
        </div>
      </Router>
    );
  }
  
  export default Admin;

  function AdminAuth () {
    return (
      <Router>
        <Switch>
          <Route path="/">
            <Login />
          </Route>
        </Switch>
      </Router>
    );
  }
  
  function AdminPanel () {
    return (
        <Router>
          <div className="Admin-panel">
            {/* Header */}
            <Header />
            {/* LeftNav */}
            <Leftbar />

            {/* A <Switch> looks through its children <Route>s and
                renders the first one that matches the current URL. */}
            <div className="App-main">
              <div className="container-fluid">
                <Switch>
                  <Route path="/customers/new">
                    <CustomerNew />
                  </Route>
                  <Route path="/customers/:id">
                    <CustomerDetail />
                  </Route>
                  <Route path="/customers">
                    <Customers />
                  </Route>
                  <Route path="/tables">
                    <Tables />
                  </Route>
                  <Route path="/collections/new">
                    <CollectionNew />
                  </Route>
                  <Route path="/collections/:id">
                    <CollectionEdit />
                  </Route>
                  <Route path="/collections">
                    <Collections />
                  </Route>
                  <Route path="/products/new">
                    <ProductNew />
                  </Route>
                  <Route path="/products/:id">
                    <ProductEdit />
                  </Route>
                  <Route path="/products">
                    <Products />
                  </Route>
                  <Route path="/categories">
                    <Categories />
                  </Route>
                  <Route path="/orders/:id">
                    <OrderDetail />
                  </Route>
                  <Route path="/orders">
                    <Orders />
                  </Route>
                  <Route path="/dashboard">
                    <Dashboard />
                  </Route>
                  {/* <Route path="/login">
                    <Login />
                  </Route> */}
                  <Route path="/">
                    <Dashboard />
                  </Route>
                </Switch>
              </div>
            </div>
          </div>
        </Router>
    );
  }