import React from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { FaArrowLeft } from 'react-icons/fa';
import PageHeader from '../components/PageHeader';
import { UserFactory, Utils } from '../../factory';

function CustomerDetail() {

    let { id } = useParams();
    let history = useHistory();
    const [customer, setCustomer] = React.useState(null);

    const remove = () => {
        Utils.showSpinner();
        UserFactory.removeCustomer(id)
            .then(res => {
                if (res.status === 200) {
                    Utils.showToast('success', 1500, res.message, '');
                    history.push('/customers');
                } else {
                    Utils.showToast('warning', 1500, res.message, '');
                }
                Utils.hideSpinner();
            })
            .catch(err => {
                console.log('err: ', err);
                Utils.showToast('error', 1500, "Error Occurred.", '');
                Utils.hideSpinner();
            })
    }
        
    React.useEffect(() => {
        Utils.showSpinner();
        UserFactory.getCustomerDetail(id)
            .then(res => {
                if (res.status === 200) {
                setCustomer(res.data);
                } else {
                Utils.showToast('warning', 1500, res.message, '');
                }
                Utils.hideSpinner();
            })
            .catch(err => {
                console.log('err: ', err);
                Utils.showToast('error', 1500, "Error Occurred.", '');
                Utils.hideSpinner();
            })
    }, [id]);

  return (
    <div className="CustomerDetail container">
        <Link className="btn-link text-dark" to={`/customers`}>
            <FaArrowLeft /><span className="pl-2">Customers</span>
        </Link>
        <PageHeader title={customer && customer.name} />
        {/*  */}
        <div className="row">
            <div className="col-8">
                <div className="card p-3">
                    <h6>{customer && customer.name}</h6>
                    <div className="text-gray">Here some short description</div>
                </div>
                <div className="card p-3 mt-3">
                    <h6>Orders placed</h6>
                    <div className="row mx-0">
                        comming soon
                    </div>
                </div>
            </div>
            <div className="col-4">
                <div className="card p-3">
                    <h6>Customer</h6>
                    <div className="separator mb-3"></div>
                    <div className="form-group">
                        <label className="d-block">CONTACT INFOMATION</label>
                        <span className="text-priamer">{customer && customer.email}</span>
                    </div>
                    <div className="separator mb-3"></div>
                    <div className="form-group">
                        <label className="d-block">Phone number</label>
                        <span className="text-priamer">{customer && customer.phone}</span>
                    </div>
                </div>
                <div className="card p-3 mt-3">
                    <h6>Customer Action</h6>
                    <div className="separator mb-3"></div>
                    <div className="row mx-0">
                        <button className="btn btn-danger ml-auto" onClick={() => remove()}>Remove</button>
                    </div>
                </div>
            </div>
        </div>
        {/*  */}
    </div>
  );
}

export default CustomerDetail;
