import React from 'react';
import { FaSearch } from 'react-icons/fa';
import { Utils, CollectionFactory, ProductFactory } from '../../factory';
import { useHistory } from 'react-router-dom';

function CollectionForm(props) {

    let history = useHistory();

    const [formType, setFromType] = React.useState('new');
    const [collection, setCollection] = React.useState({
        title: '',
        description: '',
        is_published: false,
    });
    const [collectionItems, setcollectionItems] = React.useState(null);
    const [keyward, setKeyward] = React.useState('');
    const [products, setProducts] = React.useState(null);
    const [fileEle, setFileEle] = React.useState();
    const [file, setFile] = React.useState();
    const [newFilePath, setNewFilePath] = React.useState();
 
    React.useEffect(() => {
        if (props && props.collection) {
            setCollection(props.collection);
            setcollectionItems(props.collection.collection_items);
            setFromType('edit');
        } else {
            setFromType('new');
        }
    }, [props])

    const save = () => {
        const data = new FormData();
        data.append('title', collection.title);
        data.append('description', collection.description);
        data.append('is_published', collection.is_published);
        if (file && file.size > 0) {
            data.append('image', file);
        }
        // api
        if (formType === 'edit') {
            data.append('id', collection.id);
            Utils.showSpinner();
            CollectionFactory.updateCollection(data)
                .then(res => {
                    if (res.status === 200) {
                        Utils.showToast('success', 1500, res.message, '');
                        history.push('/collections');
                    } else {
                        Utils.showToast('warning', 1500, res.message, '');
                    }
                    Utils.hideSpinner();
                })
                .catch(err => {
                    console.log('err: ', err);
                    Utils.showToast('error', 1500, "Error Occurred.", '');
                    Utils.hideSpinner();
                })
        } else {
            Utils.showSpinner();
            CollectionFactory.addCollection(data)
                .then(res => {
                    if (res.status === 200) {
                        Utils.showToast('success', 1500, res.message, '');
                        history.push('/collections');
                    } else {
                        Utils.showToast('warning', 1500, res.message, '');
                    }
                    Utils.hideSpinner();
                })
                .catch(err => {
                    console.log('err: ', err);
                    Utils.showToast('error', 1500, "Error Occurred.", '');
                    Utils.hideSpinner();
                })
        }
    }

    const remove = () => {
        if (formType === 'edit') {
            Utils.showSpinner();
            CollectionFactory.removeCollection(collection.id)
                .then(res => {
                    if (res.status === 200) {
                        Utils.showToast('success', 1500, res.message, '');
                        history.push('/collections');
                    } else {
                        Utils.showToast('warning', 1500, res.message, '');
                    }
                    Utils.hideSpinner();
                })
                .catch(err => {
                    console.log('err: ', err);
                    Utils.showToast('error', 1500, "Error Occurred.", '');
                    Utils.hideSpinner();
                })
        }
    }

    const searchProducts = (keyward) => {
        if (!keyward) {
            setProducts(null);
            return;
        }
        ProductFactory.search({search: keyward})
            .then(res => {
                if (res.status === 200) {
                    setProducts(res.data);
                } else {
                    Utils.showToast('warning', 1500, res.message, '');
                }
                Utils.hideSpinner();
            })
            .catch(err => {
                console.log('err: ', err);
                Utils.showToast('error', 1500, "Error Occurred.", '');
                Utils.hideSpinner();
            })
    }

    const addProductToCollection = (id) => {
        Utils.showSpinner();
        CollectionFactory.addProductToCollection({collection_id: collection.id, product_id: id})
            .then(res => {
                if (res.status === 200) {
                    Utils.showToast('success', 1500, res.message, '');
                    setcollectionItems(res.data);
                } else {
                    Utils.showToast('warning', 1500, res.message, '');
                }
                Utils.hideSpinner();
            })
            .catch(err => {
                console.log('err: ', err);
                Utils.showToast('error', 1500, "Error Occurred.", '');
                Utils.hideSpinner();
            })
    }

    const handleFileChangeEvent = (event) => {
        var _file = event.target.files[0];
        setFile(_file);
        // pre load image
        var reader = new FileReader();
        var url = reader.readAsDataURL(_file);
        reader.onloadend = function (e) {
            setNewFilePath(reader.result);
        };
    }

  return (
    <div className="CollectionForm">
      <div className="row">
        <div className="col-8">
            <div className="card p-3">
                <div className="form-group">
                    <label>Title</label>
                    <input type="text" className="form-control" placeholder="e.g. Summer collection, Under $100, Staff picks" 
                        value={collection.title} onChange={(event) => setCollection({...collection, title: event.target.value})}
                    />
                </div>
                <div className="form-group">
                    <label>Description</label>
                    <textarea className="form-textarea w-100" value={collection.description} onChange={(event) => setCollection({...collection, description: event.target.value})}></textarea>
                </div>
            </div>
            <div className={formType === 'edit' ? "card p-3 mt-3" : "d-none"}>
                <h6>Products</h6>
                <div className="row">
                    <div className="col-7">
                        <div className="form-group position-relative">
                            <span className="form-prefix-icon"><FaSearch /></span>
                            <input type="text" className="form-control form-search" placeholder="Search products" 
                                value={keyward}
                                onChange={(event) => setKeyward(event.target.value)}
                                onKeyUp={(event) => searchProducts(event.target.value)}
                            />
                            <ul className="search-dropdown shadow">
                                {products && products.map((item, index) => 
                                    <li className="product-item" key={index} onClick={() => addProductToCollection(item.id)}>{item.title}</li>
                                )}
                            </ul>
                        </div>
                    </div>
                    <div className="col-5">
                        <select className="form-control">
                            <option>Option1</option>
                            <option>Option2</option>
                        </select>
                    </div>
                </div>
                <div className="separator mb-3"></div>
                <ul>
                    {collectionItems && collectionItems.map((item, index) => 
                        <li className="product-item" key={index}>{item.product.title}</li>
                    )}
                </ul>
            </div>
        </div>
        <div className="col-4">
            <div className="card p-3">
                <div className="form-group">
                    <label>Publish Status</label>
                    <select className="form-control" value={collection.is_published} onChange={(event) => setCollection({...collection, is_published: event.target.value})}>
                        <option value={true}>Publish</option>
                        <option value={false}>Unpublish</option>
                    </select>
                </div>
            </div>
            <div className="card p-3 mt-3">
                <div className="form-group">
                    <label>Media</label>
                    <input type="file" className="d-none" 
                        ref={input => setFileEle(input)} 
                        onChange={(event) => handleFileChangeEvent(event)}
                        />
                    <div className="row pb-3">
                        <button className="btn btn-primary mx-auto" onClick={() => fileEle.click()}>Upload</button>
                    </div>
                    <div className="row">
                        <div className="col-6">
                            <div className="text-center">Original Upload</div>
                            <img className="w-100" src={collection.image} alt="" />
                        </div>
                        <div className="col-6">
                            <div className="text-center">New Upload</div>
                            <img className="w-100" src={newFilePath} alt="" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
      
      <div className="separator my-3"></div>
        <div className="row mx-0">
            <button className={formType === 'edit' ? "btn btn-md btn-danger mr-auto" : "d-none"} onClick={() => remove()}>Delete collection</button>
            <button className="btn btn-md btn-primary ml-auto" onClick={() => save()}>Save</button>
        </div>
    </div>
  );
}

export default CollectionForm;
