import env from '../env';
import { GET, POST } from './Utils';

const getOrders = (data) => {
    const path = `${env.BASE_API}/admin/orders`;
    return POST(path, data);
}

const getOrderDetail = (id) => {
    const path = `${env.BASE_API}/admin/orders/${id}`;
    return GET(path);
}

const OrderFactory = {
    getOrders,
    getOrderDetail
}

export default OrderFactory;