import React from 'react';
import PageHeader from '../components/PageHeader';
import { Link } from 'react-router-dom';
import { ProductFactory } from '../../factory';
import Utils from '../../factory/Utils';

function Products() {

  
  const [products, setProducts] = React.useState(null);

  React.useEffect(() => {
    getProducts();
  }, []);
  
  const getProducts = () => {
    Utils.showSpinner();
    ProductFactory.getProducts()
      .then(res => {
        if (res.status === 200) {
          setProducts(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      });
  }


  return (
    <div className="Products">
      <PageHeader title="Products" />
      {/*  */}
      <div className="card">
        <div className="card-header d-flex">
          <h6>All</h6>
          <Link className="btn btn-md btn-primary ml-auto" to={`/products/new`}>+ Add product</Link>
        </div>
        <div className="card-body p-0">
          <ul className="tb-ul">
            {products && products.map((item, index) => 
              <li className="tb-row" key={index}>
                <div className="col-6 d-flex">
                  <div className="row-avatar">
                    <img src={item.image} className="avatar" alt="" />
                  </div>
                  <div className="d-block ml-3 my-auto">
                    <div className="row-name font-weight-bold">{item.title}</div>
                    <div className="row-email text-dark">{item.description}</div>
                  </div>
                </div>
                <div className="col-1">
                  {item.currency}
                  {item.price}
                </div>
                <div className="col-1">
                  {item.category.title}
                </div>
                <div className="col-1">
                  {item.when_sold_out}
                </div>
                <div className="col-1">
                  {item.quantity}
                </div>
                <div className="col-1">
                  <span className={item.is_active ? "badge badge-pill badge-success" : "badge badge-pill badge-danger"} >{item.is_active ? "Active" : "Inactive"}</span>
                </div>
                <div className="col-1 text-right">
                  <Link to={`/products/${item.id}`} className="">Edit</Link>
                </div>
              </li>
            )}
          </ul>
        </div>
        <div className="card-footer">
          <button className="btn btn-pagination">
            <svg viewBox="0 0 20 20" className="Polaris-Icon__Svg_375hu" focusable="false" aria-hidden="true"><path d="M17 9H5.414l3.293-3.293a.999.999 0 1 0-1.414-1.414l-5 5a.999.999 0 0 0 0 1.414l5 5a.997.997 0 0 0 1.414 0 .999.999 0 0 0 0-1.414L5.414 11H17a1 1 0 1 0 0-2" fillRule="evenodd"></path></svg>
          </button>
          <button className="btn btn-pagination">
            <svg viewBox="0 0 20 20" className="Polaris-Icon__Svg_375hu" focusable="false" aria-hidden="true"><path d="M17.707 9.293l-5-5a.999.999 0 1 0-1.414 1.414L14.586 9H3a1 1 0 1 0 0 2h11.586l-3.293 3.293a.999.999 0 1 0 1.414 1.414l5-5a.999.999 0 0 0 0-1.414" fillRule="evenodd"></path></svg>
          </button>
        </div>
      </div>
      {/*  */}
    </div>
  );
}

export default Products;
