import React from 'react';
import { Link } from 'react-router-dom';
import { FaArrowLeft } from 'react-icons/fa';
import PageHeader from '../components/PageHeader';
import ProductForm from '../components/ProductForm';
import { CategoryFactory } from '../../factory';
import Utils from '../../factory/Utils';

function ProductNew() {

  const [categories, setCategories] = React.useState(null);

  React.useEffect(() => {
    getCategories();
  }, []);

  const getCategories = () => {
    Utils.showSpinner();
    CategoryFactory.getCategories()
      .then(res => {
        if (res.status === 200) {
          setCategories(res.data);
        } else {
          Utils.showToast('warning', 1500, res.message, '');
        }
        Utils.hideSpinner();
      })
      .catch(err => {
        console.log('err: ', err);
        Utils.showToast('error', 1500, "Error Occurred.", '');
        Utils.hideSpinner();
      })
  }

  return (
    <div className="ProductNew container">
        <Link className="btn-link text-dark" to={`/products`}>
            <FaArrowLeft /><span className="pl-2">Products</span>
        </Link>
        <PageHeader title="Add product" />
        {/*  */}
        <ProductForm categories={categories} product={null} />
        {/*  */}
    </div>
  );
}

export default ProductNew;
